import React, { useState } from "react";
import Header from "../components/Header";
import Table from "../components/Table";

export default function Neuralnetwork() {
  const [n, setN] = useState(3);
  const [m, setM] = useState(4);
  const [E, setE] = useState(0.01);
  const [func, setFunc] = useState(1);
  const [T, setT] = useState(0.6);
  const [K, setK] = useState(0.6);
  let w1 = [
    [0.5, 0.8],
    [0.4, 0.6],
  ];
  let w2 = [0.2, 0.7];
  const [result, setResult] = useState("");
  const [values, setValues] = useState(
    Array.from({ length: m }, () => Array.from({ length: n }, () => 0))
  );
  const updateTable = () => {
    setValues(
      Array.from({ length: m }, () => Array.from({ length: n }, () => 0))
    );
    setResult("");
    w1 = [
      [0.5, 0.8],
      [0.4, 0.6],
    ];
    w2 = [0.2, 0.7];
  };
  const cahnge = (e, index, indX, indY) => {
    let newValues = [...values];
    newValues[indX][indY] = +e.target.value;
    setValues(newValues);
  };
  function Fporog(S) {
    return S >= T ? 1 : 0;
  }
  function Fsigmo(S) {
    return 1 / (1 + Math.E ** (-S * K));
  }
  function Flin(S) {
    return S * K;
  }
  const go_forward = (inp, func) => {
    let sum = [
      w1[0][0] * inp[0] + w1[1][0] * inp[1],
      w1[0][1] * inp[0] + w1[1][1] * inp[1],
    ];
    const out = [func(sum[0]), func(sum[1])];
    sum = out[0] * w2[0] + out[1] * w2[1];
    const y = func(sum);

    return { y: y, out: out };
  };

  function train(epoch, f, epselon = 0.001) {
    const count = epoch.length;
    let e = 1;
    // for (let i = 0; i < 100; i++) {
    do {
      const x = epoch[Math.round(Math.random() * (count - 1))];
      const y = go_forward(x.slice(0, 2), f).y;
      const out = go_forward(x.slice(0, 2), f).out;
      const d = x[2];
      let sigma2 = (d - y) * y * (1 - y);
      let sigma11 = out[0] * (1 - out[0]) * sigma2 * w2[0];
      let sigma12 = out[1] * (1 - out[1]) * sigma2 * w2[1];

      w2[0] = w2[0] + T * sigma2 * out[0];
      w2[1] = w2[1] + T * sigma2 * out[1];

      w1[0][0] = w1[0][0] + T * sigma11 * x[0];
      w1[0][1] = w1[0][1] + T * sigma12 * x[0];
      w1[1][0] = w1[1][0] + T * sigma11 * x[1];
      w1[1][1] = w1[1][1] + T * sigma12 * x[1];

      e = Math.pow(d - y, 2);
    } while (epselon < e);
    // }

    console.log("w1: ", w1);
    console.log("w2: ", w2);
    console.log("e: ", e);
  }

  const algNetwork = () => {
    const epoch = [...values];
    if (func > 0 && func <= 3) {
      train(
        epoch.slice(1),
        func === 1 ? Fporog : func === 2 ? Fsigmo : Flin,
        E
      );
    } else {
      alert("Выбрана неверная функция активации!");
      return;
    }

    const data = go_forward(epoch[0], Fsigmo, E);
    setResult(
      `Значение полученной НС на выходе: ${data.y.toFixed(
        4
      )}\nЗначение полученой НС на первом слое: ${data.out.map((value) =>
        value.toFixed(4)
      )}`
    );
  };

  return (
    <>
      <Header />
      <div className="page">
        <section>
          <section className="inputs">
            <div>
              <label htmlFor="n">Число входов:</label>
              <input
                type="number"
                value={n}
                disabled
                // onChange={(e) => setN(e.target.value)}
                name="n"
              />
            </div>

            <div>
              <label htmlFor="m">Число строк выборки:</label>
              <input
                type="number"
                value={m}
                disabled
                // onChange={(e) => setM(e.target.value)}
                name="m"
              />
            </div>
            <div>
              <label htmlFor="E">Точность: </label>{" "}
              <input
                type="number"
                value={E}
                onChange={(e) => setE(e.target.value)}
                name="E"
              />
            </div>
            <div>
              <label htmlFor="Func">Функция активации: </label>{" "}
              <label htmlFor="Func">
                (1-Пороговая, 2-Сигмоидная, 3-Линейная){" "}
              </label>{" "}
              <input
                type="number"
                value={func}
                onChange={(e) => setFunc(+e.target.value)}
                name="Func"
              />
            </div>
          </section>
          <button onClick={() => updateTable()}>Сохранить/Сбросить</button>

          <div style={{ display: "flex" }}>
            <Table
              title2="строка"
              title1="значение"
              values={values}
              cahnge={cahnge}
            ></Table>
            <div>
              <p htmlFor="img">Архитектура нейросети (2-1)</p>
              <img
                src={require("../asset/struct.png")}
                alt="Архитектура нейросети"
                width={600}
              />
            </div>
          </div>
        </section>

        <section>
          <button
            onClick={() =>
              values.reduce(
                (acc, curVal) =>
                  acc + curVal.reduce((acc1, curVal1) => acc1 + curVal1, 0),
                0
              )
                ? algNetwork()
                : alert("Заполните таблицу данными")
            }
          >
            Решение
          </button>
          <h4>Результаты решения</h4>
          <textarea value={result} className="result" readOnly></textarea>
        </section>
      </div>
    </>
  );
}
